#logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#instructions {
  border-radius: 25px;
  border: 5px solid grey;
  vertical-align: middle;
  height: 200px;
  width: 450px;
  margin: 0 auto;
}
.line {
    height:3px;
    color:gray;
    background-color: gray;
}

.wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
.Card {
  width: 220px;
  margin: 10px auto;
  border: 2px solid black;
  background-color: lightgray;
  border-radius: 20px;
}

#images {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

.type {
    background-color: lightblue;
    width: 150px;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    margin: 10px auto;
    border-radius: 5px;
}
.wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}
